<style>

</style>
<template>
  <!--->-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" height="20" width="20"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">-</span>
      </iCol>
    </Row>
    <!---->
    <div class="tool-panel">
      <Row style="margin-top: 10px;">
        <!--<span></span>-->
        <Select class="grey" v-model="selectedBuildingId" clearable placeholder="" @on-change="searchDataByParam"
                style="width: 200px">
          <Option :value="building.buildingId" :key="building.buildingId" v-for="building in buildingList">
            {{ building.buildingName }}
          </Option>
        </Select>
        <Select class="grey" v-model="selectedTaskType" clearable placeholder="" @on-change="searchDataByParam"
                style="margin-left: 5px; width: 120px">
          <Option :value="'PATROL'"></Option>
          <Option :value="'UPKEEP'"></Option>
          <Option :value="'DETECT'"></Option>
        </Select>
        <Select class="grey" v-model="selectedTaskStatus" clearable placeholder="" @on-change="changeTaskStatus"
                style="margin-left: 5px; width: 120px">
          <Option :value="'OPEN'"></Option>
          <Option :value="'EXECUTING'"></Option>
          <Option :value="'DONE'"></Option>
        </Select>
        <Select class="grey" v-model="selectedTaskResult" clearable placeholder="" @on-change="searchDataByParam"
                style="margin-left: 5px; width: 120px">
          <Option :value="'isOverTime'"></Option>
          <Option :value="'PASS'"></Option>
          <Option :value="'FAIL'"></Option>
          <Option :value="'FAIL-FIXED'"></Option>
        </Select>
        <DatePicker :type="'daterange'" v-model="taskDtRange" class="grey" placeholder=""
                    split-panels style="margin-left: 5px; width: 180px" @on-change="searchDataByParam"></DatePicker>
        <Button @click="searchDataByParam" class="grey" style="margin-left:5px;width:80px;"></Button>
      </Row>
    </div>
    <!---->
    <div class="body-table">
      <!---->
      <Table :columns="columns" border :data="taskList" class="auto-height-table grey" style="width:100%">
      </Table>
      <!---->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page @on-change="searchData" :total="pageTotal" :pageSize="pageSize" class="default" :current="1"></Page>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import niceScroll from 'jquery.nicescroll';
  import server from '../../../config/api';
  import common from "../../../libs/common";
  import util from "../../../libs/util";

  export default {
    data(){
      return {
        common: common,
        pageTotal: undefined,
        pageSize: 20,
        columns: [
          {
            title: '',
//            width: '140',
            align: 'center',
            ellipsis: true,
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.taskName
                }
              }, params.row.taskName);
            }
          },
          {
            title: '',
//            width: '120',
            ellipsis: true,
            align: 'center',
            render: (h, params) => {
              return h('span', {
                domProps: {
                  title: params.row.targetBuildingName
                }
              }, params.row.targetBuildingName);
            }
          },
          {
            title: '',
            align: 'center',
            width: '70',
            render: (h, params) => {
              let result = '';
              switch(params.row.type) {
                case 'PATROL': result = ''; break;
                case 'UPKEEP': result = ''; break;
                case 'DETECT': result = ''; break;
              }
              return h('span', result);
            }
          },
          {
            title: '',
            width: '100',
            align: 'center',
            render: (h, params) => {
              return h('span', this.common.revertYDateTime(params.row.planStartDt));
            }
          },
          {
            title: '',
            width: '100',
            align: 'center',
            render: (h, params) => {
              return h('span', this.common.revertYDateTime(params.row.planEndDt));
            }
          },
          {
            title: '',
            align: 'center',
            width: '70',
            render:(h, params) => {
              let result = '';
              switch(params.row.result) {
                case 'PASS': result = ''; break;
                case 'FAIL': result = ''; break;
              }
              return h('span', result);
            }
          },
          {
            title: '',
            align: 'center',
            width: '70',
            render:(h, params) => {
              let result = '';
              switch(params.row.status) {
                case 'OPEN': result = ''; break;
                case 'EXECUTING': result = ''; break;
                case 'DONE': result = ''; break;
              }
              return h('span', result);
            }
          },
          {
            title: '',
            width: '80',
            ellipsis: true,
            align: 'center',
            render: (h, params) => {
              let primaryExecutorName = params.row.primaryExecutor === null
                || params.row.primaryExecutor === undefined ? '' : params.row.primaryExecutor.name;
              return h('span', {
                domProps: {
                  title: primaryExecutorName
                }
              }, primaryExecutorName);
            }
          },
          {
            title: '',
            width: '100',
            align: 'center',
            render: (h, params) => {
              if(params.row.checkOutDt === null) {
                return '';
              }
              return h('span', this.common.revertYDateTime(params.row.checkOutDt));
            }
          },
          {
            title: '',
            align: 'center',
            width: '80',
            render:(h, params) => {
              return h('i-button', {
                props: {
                  type: 'text',
                  size: 'small'
                },
                style: {
                  color: '#a9b2fd'
                },
                on: {
                  click: () => {
                    this.showTaskDetail(params.row.taskId);
                  }
                }
              }, '');
            }
          }
        ],
        currentOrgId: localStorage.current_org_id,
        taskList: [],
        planEndDtRange: [],
        planStartDtRange: [],
        selectedBuildingId: undefined,
        selectedTaskType: undefined,
        selectedTaskResult: undefined,
        selectedTaskStatus: undefined,
        taskDtRange: [],
        buildingList: []
      };
    },
    methods: {
      initData: function() {
        // 
        this.searchBuildingList();
        this.searchData(1);
      },
      fetchData: function() {
        this.$nextTick(() => {
          $(".auto-height-table .ivu-table-body").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      },
      changeTaskStatus: function(value) {
        if(value === 'OPEN' && this.selectedTaskResult !== 'isOverTime') {
          this.selectedTaskResult = undefined;
        }
        this.searchData(1);
      },

      searchDataByParam: function() {
        this.searchData(1);
      },
      resetPwd: function() {
        this.$http.put("/user/")
      },
      searchBuildingList: function() {
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}', this.currentOrgId)).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.buildingList = response.body.data;
        }, (error) => {
          this.$Message.error("");
        });
      },
      searchData: function(pageNum) {
        let url = server.api.routine.record.list.replace('{orgId}', this.currentOrgId) + '?orgId=' + this.currentOrgId + '&pageNum=' + pageNum + '&pageSize=' + this.pageSize;
        if(this.selectedTaskStatus !== undefined && this.selectedTaskStatus.length !== 0) {
          url += '&status=' + this.selectedTaskStatus;
        }
        if(this.selectedTaskResult !== undefined && this.selectedTaskResult.length !== 0) {
          if(this.selectedTaskResult === 'isOverTime') {
            url += '&isOverTime=true';
          } else {
            url += '&taskResult=' + this.selectedTaskResult;
          }
        }
        if(this.selectedTaskType !== undefined && this.selectedTaskType.length !== 0) {
          url += '&taskType=' + this.selectedTaskType;
        } else {
          url += '&taskType=PATROL,UPKEEP,DETECT';
        }
        if(this.selectedBuildingId !== undefined && this.selectedBuildingId.length !== 0) {
          url += '&buildingId=' + this.selectedBuildingId;
        }
        let range = [];
        if (this.taskDtRange === undefined || this.taskDtRange === null || this.taskDtRange.length === 0
          || this.taskDtRange[0] === null || this.taskDtRange[0].length === 0) {
          range = null;
        } else {
          range.push(util.dateFormat(this.taskDtRange[0], 'yyyy-MM-dd HH:mm:ss'));
          range.push(this.taskDtRange[1]);
          range[1].setDate(range[1].getDate() + 1);
          range[1].setSeconds(range[1].getSeconds() - 1);
          range[1] = util.dateFormat(range[1], 'yyyy-MM-dd HH:mm:ss');
        }
        url += range === null ? '' : '&planEndDtStart=' + range[0] + '&planEndDtEnd=' + range[1];
        this.$http.get(url).then((response) => {
          if(!response.body.meta.success) {
            this.$Message.error("");
            return;
          }
          this.pageTotal = response.body.data.totalCount;
          this.taskList = response.body.data.rows;
        }, (error)=> {
          this.$Message.error("");
        });
      },
      showTaskDetail: function(taskId) {
        this.clearYunSession('route_list');
        let page = {};
        page.router = {
          path: '/routine/task',
        };
        this.pushYunSession('route_list', page);
        // 
        page = {
          router: {
            path: '/routine/task_detail',
          },
          taskId: taskId
        };
        this.pushYunSession('route_list', page);
        this.$router.push(page.router);
      }
    },
    mounted(){
      this.initData();
      this.fetchData();
    }

  }
</script>



